.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-color: white;
}

.main-content {
  flex: 1;
  padding: 2rem;
  max-width: 1400px;
  margin: 0 auto;
  width: 100%;
  background-color: white;
}

.page {
  padding: 2rem;
  background-color: white;
  border-radius: 8px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .it-isqs-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.main-content.no-padding {
  padding: 0;
  max-width: 100%;
  overflow-x: hidden;
}

body.contact-page {
  overflow-x: hidden;
}

/* Update footer visibility with higher z-index */
.contact-page footer {
  position: relative;
  z-index: 20; /* Increased from 10 to 20 */
  background-color: white; /* Ensure footer has a background */
}
